.card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: transparent;
  border-radius: 5px;
  padding: 25px 15px;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
  transform: translateZ(0);
  transition: transform 0.25s ease-out;

  &:hover {
    transform: scale(1.05);
  }
  h1,
  p {
    color: white;
    font-family: unset;
    font-size: 2em;
  }

  img {
    width: 100%;
    height: 100%;
  }
}
